import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform } from 'ionic-angular';
import { ImageViewerController } from 'ionic-img-viewer';
/**
 * Generated class for the PerimeterPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-perimeter',
  templateUrl: 'perimeter.html',
})
export class PerimeterPage {

   _imageViewerCtrl: ImageViewerController;
 
  constructor(public navCtrl: NavController, imageViewerCtrl: ImageViewerController, public navParams: NavParams , platform: Platform) {
  
  
    let backAction =  platform.registerBackButtonAction(() => {
        console.log("contact");
        this.navCtrl.pop();
        backAction();
      },2)
  
  
    this._imageViewerCtrl = imageViewerCtrl;
  }
 
  presentImage(myImage) {
    const imageViewer = this._imageViewerCtrl.create(myImage);
    imageViewer.present();
  }

   
  

pages: any[] = [
    { title: 'ListMasterPage'}
    ]

   

  ionViewDidLoad() {
    console.log('ionViewDidLoad PerimeterPage');
  }
  
     startApp(page) {
    this.navCtrl.setRoot(page, {}, {
      animate: true,
      direction: 'forward'
    });
  
}

}



